import { Link } from "gatsby";
import React from "react";
import * as Style from "./navigation.module.css";
import {
  hamburger,
  hamburgerBox,
  hamburgerInner,
  hamburgerArrowR,
  isActive,
} from "./hamburgers.module.css";
import { fontFamily } from "../global.module.css";

type MyState = {
  isActive: boolean;
  isMobile: boolean;
};

class NavigationBar extends React.Component<any, MyState> {
  constructor(props) {
    super(props);
    this.toggleActive = this.toggleActive.bind(this);
    this.state = {
      isActive: false,
      isMobile: document.documentElement.clientWidth < 768,
    };
  }

  toggleActive() {
    let state = !this.state.isActive;
    this.setState({
      isActive: state,
    });
  }

  render() {
    let isMobile = document.documentElement.clientWidth < 768;
    if (this.state.isMobile != isMobile) {
      this.setState({
        isMobile: isMobile,
      });
    }

    let burgerStyleArray = [
      hamburger,
      hamburgerArrowR,
      Style.burger,
      Style.animation,
    ];

    let drawerStyleArray = [Style.drawer, Style.animation];

    if (this.state.isMobile === false) {
      drawerStyleArray.push(Style.activeDrawer);
    } else {
      if (this.state.isActive === true) {
        burgerStyleArray.push(isActive);
        burgerStyleArray.push(Style.activeBurger);
        drawerStyleArray.push(Style.activeDrawer);
      }
    }

    let hamburgerButton = (
      <div className={Style.burgerContainer}>
        <button
          className={burgerStyleArray.join(" ")}
          type="button"
          onClick={this.toggleActive}
        >
          <span className={hamburgerBox}>
            <span className={hamburgerInner}></span>
          </span>
        </button>
      </div>
    );

    return (
      <>
        {this.state.isMobile === true ? hamburgerButton : null}
        <nav className={drawerStyleArray.join(" ")} id="drawer">
          <ul className={[Style.list, fontFamily].join(" ")}>
            <li className={Style.listContainer}>
              <Link to="/" className={Style.listLink}>
                Home
              </Link>
            </li>
            <li className={Style.listContainer}>
              <Link to="/about" className={Style.listLink}>
                About
              </Link>
            </li>
            <li className={Style.listContainer}>
              <Link to="/experience" className={Style.listLink}>
                Experience
              </Link>
            </li>
            <li className={Style.listContainer}>
              <Link to="/skills" className={Style.listLink}>
                Skills
              </Link>
            </li>
            <li className={Style.listContainer}>
              <Link to="/contact" className={Style.listLink}>
                Contact
              </Link>
            </li>
            {/* <li>
              <Link to="/about" className={Style.listLink}>
                About
              </Link>
            </li>
            <li>
              <Link to="/skills" className={Style.listLink}>
                Skills
              </Link>
            </li>
            <li>
              <Link to="/experience" className={Style.listLink}>
                Experience
              </Link>
            </li>
            <li>
              <Link to="/contact" className={Style.listLink}>
                Contact
              </Link>
            </li> */}
          </ul>
        </nav>
      </>
    );
  }
}

export default NavigationBar;
